import React from 'react'
import '../../App.css'
import ProjectSection from '../ProjectSection';
import ProjectSectionReverse from '../ProjectSectionReverse';
import '../ProjectSection.css'
import Navbar from '../AlternativeNavbar';
import { aboutLunarLanguages, locationTracking, hotspots, practiceMode, userInformation, development, personalRole } from './LunarLanguagesData';

export default function Services() {
    return (
        <>
            <Navbar />
            <div className='project-section-div'>
                <ProjectSection {...aboutLunarLanguages} />
            </div>
            <div className='project-section-div'>
                <ProjectSectionReverse {...locationTracking} />
                <ProjectSectionReverse {...hotspots} />
                <ProjectSectionReverse {...practiceMode} />
                <ProjectSectionReverse {...userInformation} />
            </div>
            <div>
                <ProjectSection {...personalRole} />
                <ProjectSection {...development} />
            </div>
        </>
    )
}