import React from 'react'
import './ProjectSection.css'

function ProjectSection({
  exactClassName,
  topLine,
  lightText,
  lightTextDesc,
  headline,
  description,
  buttonLabel,
  img,
  heroimg,
  alt,
  imgStart
}) {
  return (
      <div
        className={exactClassName}
      >
        <div className='container'>
          <div
            className='row home__hero-row project-row'
            style={{
              display: 'flex',
              flexDirection: imgStart === 'start' ? 'row-reverse project-row' : 'row project-row'
            }}
          >
            <div className='col project-col'>
              <div className='home__hero-text-wrapper'>
                <div className='top-line'>{topLine}</div>
                <h1 className={lightText ? 'heading heading-project' : 'heading dark heading-project'}>
                  {headline}
                </h1>
                <p
                  className={
                    lightTextDesc
                      ? 'home__hero-subtitle home__hero-subtitle-project'
                      : 'home__hero-subtitle home__hero-subtitle-reverse dark'
                  }
                >
                  {description}
                </p>
              </div>
            </div>
            <div className='col project-col'>
              <div className='home__hero-img-wrapper home__hero-img-wrapper-project'>
                <img src={img} alt={alt} className={heroimg ? 'home__hero-img home__hero-img-project' : 'home__hero-gif'}/>
              </div>
            </div>
          </div>
        </div>
      </div>
  );
}

export default ProjectSection;