import React from 'react'
import styles from './HeroSection.css'
import { Button } from './Button'
import { Link } from 'react-router-dom';
import './ProjectSection.css'
import './Button.css'
import { Link as ScrollLink } from 'react-scroll';

function HeroSection({
  exactClassName,
  topLine,
  lightText,
  lightTextDesc,
  headline,
  description,
  buttonLabel,
  img,
  heroimg,
  alt,
  imgStart
}) {
  return (
      <div
        className={exactClassName}
      >
        <div className='container'>
          <div
            className='row home__hero-row hero-alt'
            style={{
              display: 'flex',
              flexDirection: 'row'
            }}
          >
            <div className='col'>
              <div className='home__hero-text-wrapper home__hero-text-wrapper-alt'>
                <div className='top-line top-line-alt'>{topLine}</div>
                <h1 className='heading heading-alt'>
                  {headline}
                </h1>
                <p className='home__hero-subtitle home__hero-subtitle-alt dark '>
                  {description}
                </p>
                <Button className='btn btn--medium'>
                    <ScrollLink to='footer' smooth={true} offset={-50}>
                    Contact Me
                    </ScrollLink>
                </Button>
              </div>
            </div>
            <div className='col'>
              <div className='home__hero-img-wrapper home__hero-img-wrapper-alt'>
                <img src={img} alt={alt} className='home__hero-img home__hero-img-alt imgborder' />
              </div>
            </div>
          </div>
        </div>
      </div>
  );
}

export default HeroSection;